        <!-- Ê≥¢ÂΩ¢Âõæ -->
        <view class="waveform">
          <view 
            class="waveform-bar" 
            v-for="(item, index) in waveformData[1]" 
            :key="index"
            :style="{ height: item + 'rpx' }"
          ></view>
        </view>
        
        <!-- ÂΩïÈü≥ÊåâÈíÆ -->
        <view class="record-icon-wrapper">
          <view 
            class="record-icon" 
            :class="{ 'recording': isRecording[1] }" 
            @tap="toggleRecord(1)"
          >
            <text class="mic-icon">üé§</text>
