        <!-- Êó∂Èó¥ÊòæÁ§∫ -->
        <view class="recording-info" v-if="isRecording[1]">
          <text class="recording-time">{{ recordTimes[1] }}Áßí</text>
        </view>
      </view>
    </view>

    <!-- Âí≥ÂóΩÂ£∞ÈááÈõÜ -->
    <view class="cough-section">
      <view class="cough-header">
        <view class="green-bar"></view>
        <text class="cough-title">Âí≥ÂóΩÂ£∞ÈááÈõÜ ({{ recordFiles[2] ? 'ÂÆåÊàêÂí≥ÂóΩÂΩïÂà∂' : 'Êú™ÂÆåÊàê' }})</text>
      </view>
      
      <view class="cough-content">
        <text class="cough-instruction">ËØ∑Âí≥ÂóΩËá≥Â∞ë‰∏âÂ£∞</text>
        
        <!-- Èü≥È¢ëÂèØËßÜÂåñ -->
        <view class="audio-visualizer">
          <view 
            class="visualizer-bar" 
