        <view class="cough-play-icon" @tap="playAudio(2)">
          <text class="play-icon">üîä</text>
        </view>
        
        <!-- ÂΩïÈü≥ÊåâÈíÆ -->
        <view class="record-icon-wrapper">
          <view 
            class="record-icon" 
            :class="{ 'recording': isRecording[2] }" 
            @tap="toggleRecord(2)"
          >
            <text class="mic-icon">üé§</text>
          </view>
        </view>
        
        <!-- ÂΩïÈü≥Êó∂Èó¥ÊòæÁ§∫ -->
        <view class="recording-info" v-if="isRecording[2]">
          <text class="recording-time">{{ recordTimes[2] }}Áßí</text>
