<script>
// import recordManager from '@/utils/record-manager.js'
import { RecordManager } from "../../utils/record-manager";

export default {
  data() {
    return {
      isRecording: [false, false, false], // 0: Ë∑üËØª1, 1: Ë∑üËØª2, 2: Âí≥ÂóΩ
      recordFiles: ['', '', ''],       // 0: Ë∑üËØª1, 1: Ë∑üËØª2, 2: Âí≥ÂóΩ
      recordTimes: [0, 0, 0],          // 0: Ë∑üËØª1, 1: Ë∑üËØª2, 2: Âí≥ÂóΩ
      recordManagers: []               // Â≠òÂÇ®ÊØè‰∏™ÂΩïÈü≥È°πÁöÑÁã¨Á´ãÁÆ°ÁêÜÂô®
    }
  },

  onLoad() {
    console.log('Ë∑üËØªÈ°µÈù¢Âä†ËΩΩ')

    // ‰∏∫ÊØè‰∏™ÂΩïÈü≥È°πÂàõÂª∫Áã¨Á´ãÁöÑÂΩïÈü≥ÁÆ°ÁêÜÂô®
    this.recordManagers = [
      new RecordManager('item_0'),  // Á¨¨‰∏Ä‰∏™Ë∑üËØªÈ°π
      new RecordManager('item_1'),   // Á¨¨‰∫å‰∏™Ë∑üËØªÈ°π
      new RecordManager('item_cough') // Âí≥ÂóΩÂΩïÈü≥È°π
    ]

    // ÂàùÂßãÂåñÊâÄÊúâÁÆ°ÁêÜÂô®
    this.recordManagers.forEach(manager => {
      manager.init()
    })
  },

  onUnload() {
    // Ê∏ÖÁêÜËµÑÊ∫ê
    this.recordManagers.forEach(manager => {
      manager.destroy()
    })
  },

  methods: {
    // Êí≠ÊîæÈü≥È¢ë
    playAudio(index) {
      console.log('Êí≠ÊîæÈü≥È¢ë', index)

      // Ëé∑ÂèñÂØπÂ∫îÂΩïÈü≥Êñá‰ª∂ÁöÑË∑ØÂæÑ
      const filePath = this.recordFiles[index]
      if (!filePath) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      // ÂàõÂª∫Èü≥È¢ë‰∏ä‰∏ãÊñá
      const audioContext = uni.createInnerAudioContext()
      audioContext.src = filePath
      audioContext.autoplay = true

      audioContext.onPlay(() => {
        console.log('ÂºÄÂßãÊí≠ÊîæÈü≥È¢ë', index)
        uni.showToast({
          title: 'Êí≠Êîæ‰∏≠',
          icon: 'none',
          duration: 1000
        })
      })

      audioContext.onEnded(() => {
        console.log('Êí≠ÊîæÁªìÊùü', index)
        audioContext.destroy()
      })

      audioContext.onError((err) => {
        console.error('Êí≠ÊîæÂ§±Ë¥•:', err)
        uni.showToast({
          title: 'Êí≠ÊîæÂ§±Ë¥•',
          icon: 'none'
        })
        audioContext.destroy()
      })
    },

    // ÂàáÊç¢ÂΩïÈü≥Áä∂ÊÄÅ
    toggleRecord(index) {
      console.log('ÂàáÊç¢ÂΩïÈü≥Áä∂ÊÄÅ', index, this.isRecording[index])

      // Ê£ÄÊü•ÊòØÂê¶ÊúâÂÖ∂‰ªñÈ°πÊ≠£Âú®ÂΩïÈü≥
      for (let i = 0; i < this.isRecording.length; i++) {
        if (i !== index && this.isRecording[i]) {
          uni.showToast({
            title: 'ËØ∑ÂÖàÂÆåÊàê‰∏ä‰∏Ä‰∏™ÂΩïÈü≥',
            icon: 'none',
            duration: 2000
          })
          return
        }
      }

      if (this.isRecording[index]) {
        // ÂÅúÊ≠¢ÂΩïÈü≥
        this.stopRecord(index)
      } else {
        // ÂºÄÂßãÂΩïÈü≥
        this.startRecord(index)
      }
    },

    // ÂºÄÂßãÂΩïÈü≥
    async startRecord(index) {
      console.log('ÂºÄÂßãÂΩïÈü≥', index)

      // ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
      uni.showLoading({
        title: 'ÂáÜÂ§áÂΩïÈü≥...',
        mask: true
      })

      try {
        // ÈáçÁΩÆÂΩïÈü≥Áä∂ÊÄÅ
        this.recordTimes[index] = 0
        this.recordFiles[index] = ''
        
        // Ëé∑ÂèñÂØπÂ∫îÂΩïÈü≥ÁÆ°ÁêÜÂô®
        const manager = this.recordManagers[index]

        // ÂêØÂä®ÂΩïÈü≥
        await manager.startRecord()

        // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
        this.isRecording[index] = true

        // ËÆæÁΩÆÊó∂Èó¥Êõ¥Êñ∞ÂõûË∞É
        manager.setTimeUpdateCallback((seconds, formatted) => {
          console.log(`ÂΩïÈü≥${index}Êó∂Èó¥Êõ¥Êñ∞:`, seconds)
          this.recordTimes[index] = seconds
        })

        uni.hideLoading()
        uni.showToast({
          title: 'ÂºÄÂßãÂΩïÈü≥',
          icon: 'none',
          duration: 1000
        })

        console.log(`ÂΩïÈü≥${index}ÂêØÂä®ÊàêÂäü`)

      } catch (error) {
        console.error('ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•:', error)
        uni.hideLoading()

        // ÊòæÁ§∫ÂÖ∑‰ΩìÈîôËØØ‰ø°ÊÅØ
        let errorMsg = 'ÂºÄÂßãÂΩïÈü≥Â§±Ë¥•'
        if (typeof error === 'string' && error.includes('ÊùÉÈôêË¢´ÊãíÁªù')) {
          errorMsg = 'ÈúÄË¶ÅÈ∫¶ÂÖãÈ£éÊùÉÈôêÔºåËØ∑Âú®ËÆæÁΩÆ‰∏≠ÂºÄÂêØ'
        } else if (typeof error === 'string' && error.includes('Ê£ÄÊü•ÊùÉÈôêÂ§±Ë¥•')) {
          errorMsg = 'ÊùÉÈôêÊ£ÄÊü•Â§±Ë¥•ÔºåËØ∑ÈáçËØï'
        } else {
          errorMsg = error.message || 'Êú™Áü•ÈîôËØØ'
        }

        uni.showToast({
          title: errorMsg,
          icon: 'none',
          duration: 3000
        })

        // ÊÅ¢Â§çÁä∂ÊÄÅ
        this.isRecording[index] = false
      }
    },

    // ÂÅúÊ≠¢ÂΩïÈü≥
    async stopRecord(index) {
      console.log('ÂÅúÊ≠¢ÂΩïÈü≥', index)

      if (!this.isRecording[index]) {
        console.warn(`ÂΩïÈü≥${index}Âπ∂Êú™Âú®ÂΩïÂà∂‰∏≠`)
        return
      }

      // ÊòæÁ§∫ÂÅúÊ≠¢‰∏≠Áä∂ÊÄÅ
      uni.showLoading({
        title: 'ÂÅúÊ≠¢ÂΩïÈü≥‰∏≠...',
        mask: true
      })

      try {
        // Ëé∑ÂèñÂØπÂ∫îÂΩïÈü≥ÁÆ°ÁêÜÂô®
        const manager = this.recordManagers[index]

        // ÂÅúÊ≠¢ÂΩïÈü≥
        await manager.stopRecord()

        // Ëé∑ÂèñÂΩïÈü≥Êñá‰ª∂Ë∑ØÂæÑ
        this.recordFiles[index] = manager.getRecordFile()

        // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
        this.isRecording[index] = false

        // Ê∏ÖÈô§Êó∂Èó¥Êõ¥Êñ∞ÂõûË∞É
        manager.setTimeUpdateCallback(null)

        uni.hideLoading()
        uni.showToast({
          title: 'ÂΩïÈü≥ÂÆåÊàê',
          icon: 'success',
          duration: 1500
        })

        console.log(`ÂΩïÈü≥${index}ÂÆåÊàêÔºåÊñá‰ª∂Ë∑ØÂæÑ:`, this.recordFiles[index])

        // Ê£ÄÊü•ÂΩïÈü≥Êñá‰ª∂ÊòØÂê¶ÊúâÊïà
        if (!this.recordFiles[index]) {
          console.warn(`ÂΩïÈü≥${index}Êñá‰ª∂Ë∑ØÂæÑ‰∏∫Á©∫`)
          uni.showToast({
            title: 'ÂΩïÈü≥Êñá‰ª∂‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
          })
        }

      } catch (error) {
        console.error('ÂÅúÊ≠¢ÂΩïÈü≥Â§±Ë¥•:', error)
        uni.hideLoading()

        uni.showToast({
          title: 'ÂÅúÊ≠¢ÂΩïÈü≥Â§±Ë¥•',
          icon: 'none',
          duration: 2000
        })

        // Âº∫Âà∂ÊÅ¢Â§çÁä∂ÊÄÅ
        this.isRecording[index] = false

        // Â∞ùËØïÊ∏ÖÁêÜËÆ°Êó∂Âô®
        const manager = this.recordManagers[index]
        if (manager) {
          manager.stopTimer()
          manager.setTimeUpdateCallback(null)
        }
      }
    },



    // Âà∑Êñ∞
    handleRefresh() {
      console.log('Âà∑Êñ∞')
      uni.showToast({
        title: 'Âà∑Êñ∞',
        icon: 'none',
        duration: 1000
      })
    },

    // ÈùôÈü≥
    handleMute() {
      console.log('ÈùôÈü≥')
      uni.showToast({
        title: 'ÈùôÈü≥',
        icon: 'none',
        duration: 1000
      })
    },

    // Ë∑≥Ëøá
    handleSkip() {
      console.log('Ë∑≥Ëøá')

      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅË∑≥ËøáÊâÄÊúâÂΩïÈü≥ÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            console.log('Áî®Êà∑Á°ÆËÆ§Ë∑≥Ëøá')

            // ÂÅúÊ≠¢ÊâÄÊúâÊ≠£Âú®ËøõË°åÁöÑÂΩïÈü≥
            this.stopAllRecording()

            // Ë∑≥ËΩ¨Âà∞‰∏ã‰∏ÄÈ°µÊàñÂÖ∂‰ªñÊìç‰Ωú
            uni.showToast({
              title: 'Â∑≤Ë∑≥Ëøá',
              icon: 'success'
            })

            // Ê®°ÊãüÂØºËà™
            setTimeout(() => {
              uni.navigateTo({
                url: '/pages/next/page'
              })
            }, 1000)
          }
        }
      })
    },

    // Á°ÆÂÆö
    async handleConfirm() {
      console.log('Á°ÆÂÆö')

      // Ê£ÄÊü•ÊòØÂê¶ÊúâÂΩïÈü≥Êú™ÂÆåÊàê
      const incompleteRecordings = this.isRecording.filter(status => status)
      if (incompleteRecordings.length > 0) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂÆåÊàêÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      // Ê£ÄÊü•ÊòØÂê¶ÊúâÂΩïÈü≥Êñá‰ª∂
      const hasRecordings = this.recordFiles.some(file => file)
      if (!hasRecordings) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂÆåÊàêËá≥Â∞ë‰∏Ä‰∏™ÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      // ÊòæÁ§∫Êèê‰∫§‰∏≠Áä∂ÊÄÅ
      uni.showLoading({
        title: 'Êèê‰∫§ÂΩïÈü≥‰∏≠...',
        mask: true
      })

      try {
        // ÂáÜÂ§á‰∏ä‰º†Êï∞ÊçÆ
        const uploadData = {
          recordings: this.recordFiles.map((file, idx) => ({
            index: idx,
            filePath: file,
            duration: this.recordTimes[idx],
            text: this.getReadingText(idx) // Ëé∑ÂèñÂØπÂ∫îÁöÑË∑üËØªÊñáÊú¨
          })),
          timestamp: Date.now()
        }

        console.log('Êèê‰∫§ÁöÑÂΩïÈü≥Êï∞ÊçÆ:', uploadData)

        // Ê®°Êãü‰∏ä‰º†ËøáÁ®ã
        await this.uploadRecordings(uploadData)

        uni.hideLoading()
        uni.showToast({
          title: 'Êèê‰∫§ÊàêÂäü',
          icon: 'success',
          duration: 2000
        })

        // Ë∑≥ËΩ¨Âà∞ÁªìÊûúÈ°µÈù¢
        setTimeout(() => {
          uni.navigateTo({
            url: '/pages/result/result'
          })
        }, 1500)

      } catch (error) {
        console.error('Êèê‰∫§Â§±Ë¥•:', error)
        uni.hideLoading()

        uni.showToast({
          title: 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      }
    },

    // ‰∏ä‰º†ÂΩïÈü≥Êñá‰ª∂ÔºàÊ®°ÊãüÔºâ
    uploadRecordings(data) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          // Ê®°ÊãüÁΩëÁªúËØ∑Ê±Ç
          const success = Math.random() > 0.2 // 80%ÊàêÂäüÁéá

          if (success) {
            console.log('ÂΩïÈü≥‰∏ä‰º†ÊàêÂäü:', data)

            // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®API
            // uni.uploadFile({
            //     url: 'API_URL',
            //     filePath: data.filePath,
            //     name: 'audio',
            //     formData: data,
            //     success: resolve,
            //     fail: reject
            // })

            resolve()
          } else {
            reject(new Error('ÁΩëÁªúÈîôËØØÔºå‰∏ä‰º†Â§±Ë¥•'))
          }
        }, 1500)
      })
    },

    // ÂÅúÊ≠¢ÊâÄÊúâÂΩïÈü≥
    stopAllRecording() {
      console.log('ÂÅúÊ≠¢ÊâÄÊúâÂΩïÈü≥')

      // ÈÅçÂéÜÊâÄÊúâÂΩïÈü≥ÁÆ°ÁêÜÂô®
      this.recordManagers.forEach((manager, index) => {
        if (this.isRecording[index]) {
          // ÂÅúÊ≠¢ÂΩïÈü≥
          manager.stopRecord()
          manager.stopTimer()
          manager.setTimeUpdateCallback(null)

          // Êõ¥Êñ∞Áä∂ÊÄÅ
          this.isRecording[index] = false

        }
      })
    },

    // Ëé∑ÂèñË∑üËØªÊñáÊú¨
    getReadingText(index) {
      const texts = [
        '‰∏Ä‰∫å‰∏âÂõõ‰∫îÂÖ≠‰∏ÉÂÖ´‰πùÂçÅ',
        'ËµµÈí±Â≠ôÊùéÔºåÂë®Âê¥ÈÉëÁéã',
        'ËØ∑Âí≥ÂóΩËá≥Â∞ë‰∏âÂ£∞'
      ]
      return texts[index] || ''
    },

    // ÈáçÊñ∞ÂΩïÂà∂ÊåáÂÆöÈ°π
    redoRecording(index) {
      console.log('ÈáçÊñ∞ÂΩïÂà∂', index)

      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈáçÊñ∞ÂΩïÂà∂Ëøô‰∏ÄÈ°πÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            // ÈáçÁΩÆÁä∂ÊÄÅ
            this.isRecording[index] = false
            this.recordFiles[index] = ''
            this.recordTimes[index] = 0

            // Â¶ÇÊûúÈúÄË¶ÅÁ´ãÂç≥ÂºÄÂßãÊñ∞ÁöÑÂΩïÈü≥
            // this.startRecord(index)

            uni.showToast({
              title: 'Â∑≤ÈáçÁΩÆÔºåÂèØ‰ª•ÈáçÊñ∞ÂΩïÈü≥',
              icon: 'success'
            })
          }
        }
      })
    },

    // Êí≠ÊîæÊâÄÊúâÂΩïÈü≥
    playAllRecordings() {
      console.log('Êí≠ÊîæÊâÄÊúâÂΩïÈü≥')

      const hasRecordings = this.recordFiles.some(file => file)
      if (!hasRecordings) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÂÆåÊàêÂΩïÈü≥',
          icon: 'none'
        })
        return
      }

      // ÊåâÈ°∫Â∫èÊí≠ÊîæÊâÄÊúâÂΩïÈü≥
      this.playRecordingSequence(0)
    },

    // È°∫Â∫èÊí≠ÊîæÂΩïÈü≥
    playRecordingSequence(index) {
      if (index >= this.recordFiles.length) {
        console.log('ÊâÄÊúâÂΩïÈü≥Êí≠ÊîæÂÆåÊàê')
        return
      }

      const filePath = this.recordFiles[index]
      if (!filePath) {
        // Â¶ÇÊûúËØ•È°πÊ≤°ÊúâÂΩïÈü≥ÔºåË∑≥Ëøá
        this.playRecordingSequence(index + 1)
        return
      }

      console.log(`Êí≠ÊîæÁ¨¨${index + 1}‰∏™ÂΩïÈü≥`)

      const audioContext = uni.createInnerAudioContext()
      audioContext.src = filePath
      audioContext.autoplay = true

      audioContext.onEnded(() => {
        console.log(`Á¨¨${index + 1}‰∏™ÂΩïÈü≥Êí≠ÊîæÁªìÊùü`)
        audioContext.destroy()

        // Êí≠Êîæ‰∏ã‰∏Ä‰∏™
        this.playRecordingSequence(index + 1)
      })

      audioContext.onError((err) => {
        console.error(`Êí≠ÊîæÁ¨¨${index + 1}‰∏™ÂΩïÈü≥Â§±Ë¥•:`, err)
        audioContext.destroy()

        // ÁªßÁª≠Êí≠Êîæ‰∏ã‰∏Ä‰∏™
        this.playRecordingSequence(index + 1)
      })
    }
  }
}
</script>

